<template>
  <div id="checkbox">
    <div :class="editable ? 'hold' : ''" class="pt-2 pb-1">
      <div v-if="editable" class="px-3">
        <v-text-field
          class="font-weight-bold"
          v-model="data.Text"
          v-on:keyup.enter="editing(false)"
          dense
          @focus="focusable"
        ></v-text-field>
        <div v-for="(item, i) in data.Option" :key="i">
          <v-text-field
            class="my-2"
            prepend-icon="mdi-checkbox-blank-outline"
            v-model="data.Option[i].Text"
            v-on:keyup.enter="editing(false)"
            dense
            @focus="focusable"
          >
            <template slot="append-outer">
              <v-btn
                @click="
                  addChild(i);
                  getQID();
                  added = !added;
                "
                color="success"
                text
                :key="i"
                >Child</v-btn
              >
              <v-icon
                @click="
                  change();
                  data.Option.splice(i, 1);
                  editable = -1;
                "
                >mdi-delete</v-icon
              >
            </template>
          </v-text-field>
          <AddChild :index="i" :data="item" :change="change" :getQID="getQID"></AddChild>
        </div>
        <v-flex class="mx-3">
          <v-row>
            <v-col style="height: 40px" class="drag pa-0" cols="2">
              <v-btn
              elevation="0"
                class="ma-0"
                color="success"
                width="100"
                v-on:click="
                  data.Option.push({
                    QID: '',
                    Text: 'Checkbox ' + (data.Option.length + 1),
                  });
                  getQID();
                "
                >+ Thêm</v-btn
              >
            </v-col>
            <v-spacer></v-spacer>
            <toolbax
              :remove="remove"
              :change="change"
              :getQID="getQID"
              @editable="editing(false); editable = $event"
            ></toolbax>
          </v-row>
        </v-flex>
      </div>

      <div v-if="!editable" @click="editing(true)">
        <div class="text-start mb-3 font-weight-bold">{{ data.Text }}</div>
        <div v-for="(item, i) in data.Option" :key="i">
          <v-checkbox class="handle my-1 py-0" :label="item.Text" />
        </div>
        <draggable
          v-model="data.Option"
          element="span"
          @sort="getQID()"
          :group="{ name: 'question' }"
          :sort="false"
          handle=".handle"
          v-bind="dragOptions"
        >
          <!-- <transition-group type="transition" class="py-0" group tag="div">
          <div v-for="(item,i) in data.Option" :key="i">
            <v-checkbox class="handle my-0 py-0" :label="item.Text" />
          </div>
        </transition-group> -->
        </draggable>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import AddChild from "./child/AddChild";
import toolbax from "./layout/bottom-toolbar";

export default {
  name: "CheckBox",
  props: {
    change: Function,
    getQID: Function,
    data: {
      GroupID: String,
      Text: String,
      Option: Array,
    },
    index: Number,
    editable: {
      type: Boolean,
      default: false,
    }
  },
  data: function () {
    return {
      // editable: false,
      added: false,
    };
  },
  methods: {
    editing(value){
      this.removeChild();
      this.$emit('isEditting', value);
    },
    remove() {
      this.$emit("remove", this.index);
    },
    focusable(e) {
      e.target.select();
    },
    addChild(index) {
      this.data.Option = this.data.Option.map((option, idx) => {
        if (index === idx) {
          return {
            ...option,
            Des: "",
            Question: [],
          };
        }
        return option;
      });
    },
    removeChild() {
      this.data.Option.map((val) => {
        if (val.Question != undefined && val.Question.length == 0) {
          this.$delete(val, 'Question')
          this.$delete(val, 'Des')
        }
      });
    },
  },
  components: {
    draggable,
    AddChild,
    toolbax,
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>