<template>
  <div>
    <v-col v-if="delShow" style="height: 40px" class="d-inline drag pa-0 mr-0" cols="2">
      <v-btn
        elevation="0"
        class="ma-0"
        color="error"
        @click="
          change();
          remove();
          getQID();
          edited();
        "
      >
        <v-icon>mdi-delete</v-icon>
        {{deleteContent}}
      </v-btn>
    </v-col>
    <v-col v-if="saveShow" style="height: 40px" class="d-inline drag pa-0 ml-2" cols="2">
      <v-btn elevation="0" class="ma-0" color="primary" width="80" @click="edited">
        <v-icon>mdi-content-save-outline</v-icon>
        {{savecontent}}
      </v-btn>
    </v-col>
  </div>
</template>

<script>
export default {
  props: {
    getQID: Function,
    remove: Function,
    change: Function,
    deleteContent: String,
    savecontent: String,
    delShow: {
      type: Boolean,
      default: true
    },
    saveShow: {
      type: Boolean,
      default: true
    },
  },
  methods: {
    edited: function () {
      this.$emit("editable", false);
    },
  },
};
</script>

