<template>
  <div id="marktable">
    <div :class="editable ? 'hold px-3 pt-1 pb-3' : ''">
      <div v-if="editable" class="grey lighten-4 px-3 mt-2 my-3 pt-2 pb-1 rounded">
        <v-text-field
          class="my-2 font-weight-bold"
          v-model="data.Text"
          v-on:keyup.enter="editable = !editable; editing(false)"
          dense
          @focus="focusable"
        ></v-text-field>
        <v-textarea
          rows="2"
          auto-grow
          label="Chỉnh sửa mô tả"
          class="my-2"
          v-model="data.Des"
          dense
        />
        <div class="mb-2 caption font-weight-medium primary--text">
          Chỉnh sửa nội dung mức điểm
        </div>
        <div>
          <v-select
            v-model="scoreVal"
            class="my-0"
            style="max-width: 25%"
            :items="[1, 2, 3, 4, 5, 6]"
            :label="scoreVal.toString()"
            @change="handleScoreColumn"
            solo
            outlined
            flat
            dense
          ></v-select>
        </div>
        <v-row no-gutters>
          <div>{{ updateRate }}</div>
          <v-col v-for="(item, i) in data.ValueArray" :key="i">
            <div
              class="py-2 text-center primary white--text font-weight-bold"
              outlined
            >
              {{ item.Value }}
            </div>
            <div class="px-2 grey lighten-2">
              <v-textarea
                rows="2"
                auto-grow
                v-on:keyup.enter="editable = !editable;"
                v-model="item.Text"
                required
                @focus="focusable"
              ></v-textarea>
            </div>
          </v-col>
        </v-row>
        <v-flex class="mx-3 mt-2">
          <v-row>
            <v-col style="height: 40px" class="drag pa-0" cols="2">
            </v-col>
            <v-spacer></v-spacer>
            <toolbax
              deleteContent="Xóa bảng"
              :remove="remove"
              :change="change"
              :getQID="getQID"
              @editable="
                editing(false);
                coverty();
                editable = $event;
              "
            ></toolbax>
          </v-row>
        </v-flex>
      </div>
      <div
        @click="
          editable = !editable;
          editing(true);
        "
        v-if="!editable"
      >
        <div class="text-start mt-2 font-weight-bold" v-text="data.Text"></div>
        <div class="text-start my-3" v-html="data.Des" />
      </div>
      <div v-for="(item, i) in data.Topic" :key="i">
        <v-card flat class="mb-2 pb-3" align="left">
          <div>
            <div v-if="edititem == item" :class="(edititem == item) ? 'hold' : ''" class="px-3 pt-2 pb-1">
              <v-text-field
                class="mb-2 font-weight-bold"
                v-model="item.Name"
                v-on:keyup.enter="edititem = !item"
                @focus="focusable"
                autofocus
              />
              <div v-for="(ques, j) in item.Question" :key="j">
                <v-text-field
                  class="mb-2"
                  hide-details="true"
                  v-model="ques.Text"
                  v-on:keyup.enter="edititem = !item"
                  dense
                  solo
                >
                  <template slot="append">
                    <v-icon
                      @click="
                        change();
                        item.Question.splice(j, 1);
                        getQID();
                      "
                      >mdi-delete</v-icon
                    >
                  </template>
                </v-text-field>
              </div>
              <v-flex class="mx-3 mt-4">
                <v-row>
                  <v-col style="height: 40px" class="drag pa-0" cols="2">
                    <v-btn
                    elevation="0"
                      class="ma-0"
                      color="success"
                      @click="
                        item.Question.push({
                          QID: '',
                          Text: item.Question.length + 1 + '. ',
                        });
                        getQID();
                      "
                      >+ Thêm</v-btn
                    >
                  </v-col>
                  <v-spacer></v-spacer>
                  <v-btn elevation="0" class="ma-0" color="error" @click="removeTopic(i)">
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                  <toolbax
                    :change="change"
                    :getQID="getQID"
                    @editable="
                      editing(false);
                      edititem = $event;
                    "
                    :delShow="false"
                  ></toolbax>
                </v-row>
              </v-flex>
            </div>
            <div
              v-if="edititem != item"
              @click="
                edititem = item;
                editing(true);
              "
            >
              <div class="font-weight-bold green--text">
                {{ item.Name }}
              </div>
              <div class="handle mb-4" v-for="(ques, j) in item.Question" :key="j">
                <div>{{ ques.Text }}</div>
                <point :data="data.ValueArray"></point>
              </div>
            </div>
          </div>
        </v-card>
      </div>
      <!-- </transition-group>
      </draggable> -->
      <v-btn
        small
        color="blue"
        v-on:click="
          data.Topic.push({
            GroupID: '',
            Name: 'TIÊU ĐỀ CÂU HỎI',
            Question: [
              {
                QID: '',
                Text: '1. Câu hỏi',
              },
            ],
          });
          getQID();
        "
        outlined
        ><v-icon small>mdi-plus</v-icon> Thêm câu hỏi</v-btn
      >
    </div>
  </div>
</template>

<script>
import toolbax from "./layout/bottom-toolbar";
import point from './preview/points';

export default {
  name: "marktable",
  props: {
    change: Function,
    getQID: Function,
    data: {
      Text: String,
      Des: String,
      ValueArray: Array,
      Topic: Array,
    },
    index: Number,
  },
  data: function () {
    return {
      scoreVal: 5,
      editable: false,
      edititem: {},
    };
  },
  methods: {
    removeTopic(i) {
      this.change();
      this.data.Topic.splice(i, 1);
      this.editing(false);
      this.getQID();
    },
    editing(value) {
      this.$emit("isEditting", value);
    },
    remove() {
      this.$emit("remove", this.index);
    },
    handleScoreColumn() {
      let ori = this.data.ValueArray.length;
      const score = this.scoreVal;
      console.log(ori, score);
      if (ori >= score) {
        this.data.ValueArray.splice(score, ori - score);
        return;
      }
      while (ori < score) {
        this.data.ValueArray.push({
          Value: this.data.ValueArray.length + 1,
          Text: "Text",
        });
        ori = this.data.ValueArray.length;
      }
    },
    coverty() {
      const mota = this.data.Des;
      this.data.Des = mota.replace(/[\n\r]/g, "<br/>");
    },
    focusable(e) {
      e.target.select();
    },
  },
  components: {
    toolbax,
    point
  },
  computed: {
    updateRate() {
      // vm.data.Des = `${this.data.Des.split("<br/>")[0]}<br/>1. ${this.data.ValueArray[0].Text} <br/>2. ${this.data.ValueArray[1].Text} <br/>3. ${this.data.ValueArray[2].Text} <br/>4. ${this.data.ValueArray[3].Text}<br/>5. ${this.data.ValueArray[4].Text}`
      return "";
    },
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
<style scoped>
.square {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 50px;
}

.hold {
  /* border: 2px solid rgba(128, 128, 128, 0.425); */
  outline: 2px solid rgb(0, 162, 255);
}

.headbar:active {
  background-color: #f1f1f1;
}
</style>